import React, { Component } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Icon from '@material-ui/core/Icon';
import IconButton from '@material-ui/core/IconButton';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';




class CustomTable extends Component{


  constructor(){
      super()
        this.state = {
          data: [],
          data_temp:[],
          currentPage : 1,
          totalRows : 5,
          sortType : [0,0,0,0]
        }

    }

    componentDidMount() {
    fetch('https://jsonplaceholder.typicode.com/users')
      .then(response => response.json())
      .then(data => this.setState({ data, data_temp:data }));
  }

  handleClickNext=()=> {
    if( Math.ceil(this.state.data.length / this.state.totalRows)>this.state.currentPage)
   this.setState({
     currentPage: this.state.currentPage+1
   });
 }

 handleClickBack=()=> {
  this.setState({
    currentPage: this.state.currentPage===1?1:this.state.currentPage-1
  });
}


filterData=(e)=> {
 var q = e.target.value;

 if(q.length===0)
 this.setState({data:this.state.data_temp})

 else{
   console.log('a')
   let data = this.state.data;
   data=data.filter(function(a){
     if(a.name.includes(q) || a.phone.includes(q) || a.username.includes(q) || a.company.name.includes(q) )
     {
       return true;
     }
   })

   this.setState({data})
 }

}

 handleChange=(e)=>{
    this.setState({totalRows:e.target.value});
 }

 sort=(a)=>{
   let data =  this.state.data
   let sortType = this.state.sortType
   switch(a){
     case 1:
     if(sortType[0]===0){
       sortType[0]=1
    data.sort(function(a,b){
       return a.name<b.name? -1 : 1
     })
}
     else{
       sortType[0]=0
     data.sort(function(a,b){
        return a.name>b.name? -1 : 1
      })
}

     this.setState({data, sortType})
     break;

     case 2:
     if(sortType[1]===0){
       sortType[1]=1
    data.sort(function(a,b){
       return a.username<b.username? -1 : 1
     })
}
     else{
       sortType[1]=0
     data.sort(function(a,b){
        return a.username>b.username? -1 : 1
      })
}

     this.setState({data, sortType})
     break;

     case 3:
     if(sortType[2]===0){
       sortType[2]=1
    data.sort(function(a,b){
       return a.phone<b.phone? -1 : 1
     })
}
     else{
       sortType[2]=0
     data.sort(function(a,b){
        return a.phone>b.phone? -1 : 1
      })
}

     this.setState({data, sortType})
     break;

     case 4:
     if(sortType[3]===0){
       sortType[3]=1
    data.sort(function(a,b){
       return a.company.name<b.company.name? -1 : 1
     })
}
     else{
       sortType[3]=0
     data.sort(function(a,b){
        return a.company.name>b.company.name? -1 : 1
      })
}

     this.setState({data, sortType})
     break;

     default:
     break;
   }
  console.log(a)
 }

  render(){

    var rows = this.state.data;
    const {currentPage, totalRows} = this.state;

    const indexOfLastTodo = currentPage * totalRows;
    const indexOfFirstTodo = indexOfLastTodo - totalRows;
    const currentTodos = rows.slice(indexOfFirstTodo, indexOfLastTodo);



    const pageNumbers = [];
   for (let i = 1; i <= Math.ceil(rows.length / totalRows); i++) {
     pageNumbers.push(i);
   }

   const renderPageNumbers = pageNumbers.map(number => {
     return (
       <li
         key={number}
         id={number}
         onClick={this.handleClick}
       >
         {number}
       </li>
     );
   });


    return(
      <MuiThemeProvider>

      <div>
      <AppBar
         title="Tabular Data"
       />

      <Paper className={useStyles.root}>
      <div style={{float:'right'}}><input onChange={this.filterData} placeholder="Search" type="text"/></div>
      <Table className={useStyles.table}>
      <TableHead>
          <TableRow>
            <TableCell style={{cursor:'pointer'}} onClick={()=> this.sort(1)}>Full Name</TableCell>
            <TableCell style={{cursor:'pointer'}} onClick={()=> this.sort(3)} align="right">Phone</TableCell>
            <TableCell style={{cursor:'pointer'}} onClick={()=> this.sort(2)} align="right">Username</TableCell>
            <TableCell style={{cursor:'pointer'}} onClick={()=> this.sort(4)} align="right">Company</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
         {currentTodos.map(row => (
           <TableRow key={row.id}>
             <TableCell component="th" scope="row">
               <div>{row.name}</div>
               {row.email}
             </TableCell>
              <TableCell align="right">{row.phone}</TableCell>
             <TableCell align="right">{row.username}</TableCell>
             <TableCell align="right">{row.company.name}</TableCell>
           </TableRow>
         ))}
       </TableBody>
      </Table>




       <div className={useStyles.footer}>
       &nbsp;Items  &nbsp;
      <select
         value={this.state.totalRows}
         onChange={this.handleChange}
       >
         <option value="1">1</option>
         <option value="2">2</option>
         <option value="3">3</option>
         <option value="4">4</option>
         <option value="5">5</option>
         <option value="6">6</option>
         <option value="7">7</option>
         <option value="8">8</option>
         <option value="9">9</option>
         <option value="10">10</option>
       </select>

          <div style={{display:'flex',float:'right'}}>
          <div style={{cursor:'pointer'}}  onClick={this.handleClickBack}>Back</div>&nbsp;
           {this.state.currentPage}&nbsp;
          <div style={{cursor:'pointer'}} onClick={ this.handleClickNext}>Next</div>&nbsp;
          </div>
       </div>
          </Paper>

          </div>

         <div>*Click on column title to sort.</div>

          </MuiThemeProvider>

    )
  }
}

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing(3),
    overflowX: 'auto',
  },
  table: {
    minWidth: 650,
  },
  button: {
   margin: theme.spacing(1),
 },
 leftIcon: {
   marginRight: theme.spacing(1),
 },
 rightIcon: {
   marginLeft: theme.spacing(1),
 },
 iconSmall: {
   fontSize: 20,
 },
 footer : {
   fontSize :12,

 },
 arrowup: {
    width: 0,
    height: 0,
    borderLeft: 15 ,
    borderRight: 15 ,
    borderBottom: 15
  }
}));

export default CustomTable;
