import React, { Component } from 'react';
import './style.css';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';




class Dragdrop extends Component {
constructor(props){
  super(props);
  this.state={
    tasks: [
      {id: "1", taskName:"Read book",type:"inProgress", backgroundColor: "red"},
      {id: "2", taskName:"Pay bills", type:"inProgress", backgroundColor:"green"},
      {id: "3", taskName:"Go to the gym", type:"Done", backgroundColor:"blue"},
      {id: "4", taskName:"Play baseball", type:"Done", backgroundColor:"green"}
	],

  selected : 0
  }
 }

 onClickLeft = ()=>{
   const {tasks, selected}=this.state;
     if(tasks[selected-1].type==="Done")
       tasks[selected-1].type = "inProgress";

       this.setState({selected:0,tasks})
 }

 onClickRight = ()=>{
   const {tasks, selected}=this.state;
     if(tasks[selected-1].type==="inProgress")
       tasks[selected-1].type = "Done";

       this.setState({selected:0,tasks})

 }



 onDragStart = (event, taskName) => {
    	console.log('dragstart on div: ', taskName);
    	event.dataTransfer.setData("taskName", taskName);
	}
	onDragOver = (event) => {
	    event.preventDefault();
	}

	onDrop = (event, cat) => {
	    let taskName = event.dataTransfer.getData("taskName");

	    let tasks = this.state.tasks.filter((task) => {
	        if (task.taskName == taskName) {
	            task.type = cat;
	        }
	        return task;
	    });

	    this.setState({
	        ...this.state,
	        tasks
	    });
	}
	render() {
		var tasks = {
	      inProgress: [],
	      Done: []
	    }

		this.state.tasks.forEach ((task) => {
		  tasks[task.type].push(
		    <div key={task.id}
		      onDragStart = {(event) => this.onDragStart(event, task.taskName)}
		      draggable
          onClick = {
            ()=> {
              this.setState({selected:task.id})
           }}
		      className="draggable"
		      style = {{backgroundColor:task.id===this.state.selected? 'red': task.bgcolor}}>
		      {task.taskName}
		    </div>
		  );
		});

	    return (
        <MuiThemeProvider>

        <div>
        <AppBar
           title="Drag & Drop"
         />
	      <div className="drag-container">

		    <div className="inProgress"
	    		onDragOver={(event)=>this.onDragOver(event)}
      			onDrop={(event)=>{this.onDrop(event, "inProgress")}}>
	          <span className="group-header">Group A</span>
	          {tasks.inProgress}
	        </div>

	        <div className="droppable"
	        	onDragOver={(event)=>this.onDragOver(event)}
          		onDrop={(event)=>this.onDrop(event, "Done")}>
	          <span className="group-header">Group B</span>
	          {tasks.Done}
	        </div>

          <div className="buttonContainer">

 <div><button ><img onClick={this.onClickLeft} width="50px" height="50px" src="./img/left.png"  alt="" /></button></div>
 <div><button ><img onClick={this.onClickRight} width="50px" height="50px" src="./img/right.png" alt=" "  /></button></div>
</div>
	      </div>

        </div>
</MuiThemeProvider>

	    );
  	}
 }


export default Dragdrop;
